import React, { useState } from 'react'
import { Row, Col, Container, NavLink, TabContent, TabPane, NavItem, Nav } from 'reactstrap/lib'
import FullStack from './FullStack';
import ReactComponent from './ReactComponent';
import JavaScriptComponent from './JavaScriptComponent';
import Button from 'reactstrap/lib/Button';

export default function Projects() {

  //toggle
  const [activeTab, setActiveTab] = useState("1");
  const toggle = (tab) => {
    if (activeTab !== tab) {
      setActiveTab(tab);
    }
  };

  return (
    <div className="section section-white section-nucleo-icons" id="projects">
      <Container>

        <Row>
          <Col className="ml-auto mr-auto text-center" md="8">
            <h2 className="title">My Projects</h2>
            <p className="description">
              Most of my projects are written in MERN stack (MongoDD, Express, React, Nodejs). However, I am currently working on a projects using ASP.NET / C# for its API and React / Reactstrap for its front-end. It will be available soon.
              </p>
          </Col>
        </Row>

        <div className="nav-tabs-navigation" >
          <div className="nav-tabs-wrapper" >
            <Nav id="tabs" role="tablist" tabs style={{ display: "flex", flexDirection: "row", justifyContent: "center", alignContent: "center" }}>

              <NavItem>
                <NavLink
                  className={activeTab === "1" ? "active" : ""}
                  onClick={() => {
                    toggle("1");
                  }}
                // className="mr-1 pr-0 ml-0 pl-0"
                >
                  <Button className="btn-round" color="primary" >Full Stack</Button>

                </NavLink>
              </NavItem>

              <NavItem >
                <NavLink
                  className={activeTab === "2" ? "active" : ""}
                  onClick={() => {
                    toggle("2");
                  }}
                // className="mr-1 pr-0 ml-0 pl-0"

                >
                  <Button className="btn-round" color="primary" >React</Button>
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink
                  className={activeTab === "3" ? "active" : ""}
                  onClick={() => {
                    toggle("3");
                  }}
                // className="mr-1 pr-0 ml-0 pl-0"
                >
                  <Button className="btn-round" color="primary" >JavaScript</Button>
                </NavLink>
              </NavItem>

            </Nav>
          </div>
        </div>

        <TabContent activeTab={activeTab} className="text-center">

          <TabPane tabId="1">
            <FullStack />
          </TabPane>

          <TabPane tabId="2">
            <ReactComponent />
          </TabPane>

          <TabPane tabId="3">
            <JavaScriptComponent />
          </TabPane>

        </TabContent>
      </Container>
    </div >
  )
}
